.hexagon {
  position: relative;
  width: 50px; 
  height: 28.87px;
  background-color: #0042aa;
  margin: 14.43px 0;
}

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
}

.hexagon:before {
  bottom: 100%;
  border-bottom: 14.43px solid #0042aa;
}

.hexagon:after {
  top: 100%;
  width: 0;
  border-top: 14.43px solid #0042aa;
}
