.grid-container {
    width: 100%;
    height: 600px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 25% 35%;
    grid-template-areas: "invoice""diagram""overview";
}

.invoice {
    margin: 0px 5%;
    grid-area: invoice;
}

.diagram {
    margin: 0px 5%;
    grid-area: diagram;
}

.overview {
    margin: 5%;
    margin-top: 15px;
    grid-area: overview;
}

@media only screen and (max-width: 1350px) {
